.home-container {
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  background-color: #f9f5f2;
}

.background-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.4)), url('/public/images/back2.jpg');
  background-size: cover;
  background-position: center;
  filter: brightness(0.9);
  z-index: 0;
}

.content-overlay {
  position: relative;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  z-index: 10;
  padding: 0 20px;
  color: #fff;
}

/* LOGO */
.logo-container {
  width: 100%;
  max-width: 350px;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.logo {
  width: 300px;
  max-height: 300px;
  object-fit: contain;
 
}

@keyframes logoFloat {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-8px);
  }
  100% {
    transform: translateY(0);
  }
}

/* HERO TEXT */
.text-content {
  max-width: 800px;
  margin-bottom: 2rem;
  padding: 0 10px;
}

.collection-tag {
  font-size: 1rem;
  letter-spacing: 3px;
  font-weight: 400;
  color: #f9d4c2;
  margin-bottom: 0.5rem;
  text-transform: uppercase;
}

.hero-title {
  font-size: 4rem;
  font-weight: 700;
  margin-bottom: 1rem;
  text-shadow: 2px 2px 15px rgba(0, 0, 0, 0.6);
  font-family: 'Playfair Display', serif;
}

.hero-subtitle {
  font-size: 1.5rem;
  margin-bottom: 2.5rem;
  max-width: 600px;
  line-height: 1.6;
  text-shadow: 1px 1px 8px rgba(0, 0, 0, 0.6);
  font-weight: 300;
}

/* BUTTONS */
.button-group {
  display: flex;
  gap: 15px;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
  max-width: 500px;
}

.primary-btn, .secondary-btn {
  padding: 12px 30px;
  font-size: 1rem;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.4s ease;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-weight: 500;
  position: relative;
}

.primary-btn {
  background: #c86b52;
  color: white;
  border: none;
  animation: buttonPulse 2s infinite;
}

@keyframes buttonPulse {
  0% {
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  }
  50% {
    box-shadow: 0 4px 20px rgba(200, 107, 82, 0.5);
    transform: scale(1.03);
  }
  100% {
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  }
}

.secondary-btn {
  background: transparent;
  color: white;
  border: 1px solid white;
}

.primary-btn:hover, .secondary-btn:hover {
  transform: translateY(-3px);
}

/* FEATURED SECTION */
.featured-section {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 20px;
}

.feature {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 0.9rem;
  letter-spacing: 1px;
  text-transform: uppercase;
}

.feature-icon {
  color: #f9d4c2;
  font-size: 1.2rem;
}

/* MEDIA QUERIES */
@media (max-width: 1024px) {
  .hero-title {
    font-size: 3rem;
  }
  .hero-subtitle {
    font-size: 1.3rem;
  }
  .logo-container {
    max-width: 300px;
  }
}

@media (max-width: 768px) {
  .hero-title {
    font-size: 2.5rem;
  }
  .hero-subtitle {
    font-size: 1.2rem;
  }
  .logo-container {
    max-width: 250px;
  }
  .primary-btn, .secondary-btn {
    padding: 10px 20px;
    font-size: 0.9rem;
  }
}

@media (max-width: 576px) {
  .content-overlay {
    padding-top: 10vh;
  }
  .hero-title {
    font-size: 2rem;
  }
  .hero-subtitle {
    font-size: 1rem;
    line-height: 1.5;
  }
  .logo-container {
    max-width: 200px;
  }
  .primary-btn, .secondary-btn {
    width: 100%;
  }
  .button-group {
    flex-direction: column;
    max-width: 250px;
  }
}

@media (max-width: 375px) {
  .hero-title {
    font-size: 1.8rem;
  }
  .hero-subtitle {
    font-size: 0.9rem;
  }
  .logo-container {
    max-width: 180px;
  }
  .primary-btn, .secondary-btn {
    font-size: 0.8rem;
  }
}
@media (max-width: 576px) {
  .content-overlay {
    display: flex;
    flex-direction: column;
    justify-content: center; /* Center align content */
    align-items: center;
    padding: 10vh 10px;
    text-align: center; /* Ensure text is centered */
  }

  .logo-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%; /* Ensure logo is centered */
    height: auto;
    max-width: 250px;
  }

  .logo {
    width: 100%;
    height: auto;
    max-height: 120px;
  }

  .hero-title {
    font-size: 2.3rem;
    text-align: center;
    max-width: 90%;
  }

  .hero-subtitle {
    font-size: 1rem;
    text-align: center;
    max-width: 85%;
    line-height: 1.5;
    margin-bottom: 1.5rem;
  }

  .button-group {
    display: flex;
    flex-direction: column; /* Stack buttons */
    align-items: center;
    gap: 10px;
    width: 100%;
    max-width: 250px;
  }

  .primary-btn, .secondary-btn {
    width: 100%; /* Make buttons full width */
    font-size: 1rem;
    padding: 12px 20px;
  }

  .featured-section {
    display: flex;
    flex-direction: column;
    align-items: center; /* Centering feature icons */
    gap: 10px;
    margin-top: 15px;
  }

  .feature {
    text-align: center;
    font-size: 0.85rem;
  }
}
